import Vue from 'vue'
import Router from 'vue-router'
import Alert from '@/components/Alert'
import Index from '@/components/Index'
import Activity from '@/components/Activity.vue'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/Alert',
      name: 'Alert',
      component: Alert
    },{
			path:'/Activity',
			name:'Activity',
			component:Activity
			}
  ]
})



