import axios from 'axios' //引用axios
import {
	Promise
} from 'es6-promise' //引入Promise

//axios 配置
axios.defaults.timeout = 5000
axios.defaults.baseURL = "http://192.168.1.48:10188/ActivityManager/";

//http request 拦截配置
axios.interceptors.request.use(
	function (config) {
		//在请求发出之前进行一些操作
		const token = sessionStorage.getItem("token"); //获取存储在本地的token
		config.data = JSON.stringify(config.data);
		config.headers = {
			'Content-Type': 'application/json' //设置跨域头部,虽然很多浏览器默认都是使用json传数据，但咱要考虑IE浏览器。
		};
		if (token) {
			config.headers.Authorization = "Token " + token; //携带权限参数
		}
		return config;
	},
	function (err) {
		//Do something with request error
		return Promise.reject(error);
	}
);

// http response 拦截器（所有接收到的请求都要从这儿过一次）
axios.interceptors.response.use(
	function (response) {
		if (response.status == 401) {
			router.push({ //push后面是一个参数对象，可以携带很多参数，具体可以去vue-router上查看，例如query字段表示携带的参数
				path: '/login'
			})
		}
		return response;
	},
	function (error) {
		return Promise.reject(error.response.data)
	}
);

export default axios;

//封装方法
/**
 * get方法
 */
export function get(url, params = {}) {
	return new Promise(function (resolve, reject) {
		axios.get(url, {
			params: params
		}).then(function (respone) {
			resolve(respone.data)
		}).catch(function (err) {
			reject(err)
		})
	});
}

/**
 * post 请求方法
 */
export function post(url, params = {}) {
	return new Promise(function (resolve, reject) {
		axios.post(url, params).then(function (respone) {
			resolve(respone.data)
		}).catch(function (err) {
			reject(err)
		})
	});
}



