<template>
	<div id="app">
		<el-container class="container">
			<el-header>
				<el-row>
					<el-col :span="12">
						<div class="left">
							<i class="el-icon-menu" @click="collapse"></i>
						</div>
					</el-col>
					<el-col :span="12">
						<div class="right">
							<el-dropdown>
								<span class="el-dropdown-link">
									Admin
									<i class="el-icon-arrow-down el-icon--right"></i>
								</span>
								<el-dropdown-menu slot="dropdown">
									<el-dropdown-item>查看</el-dropdown-item>
									<el-dropdown-item>新增</el-dropdown-item>
									<el-dropdown-item>删除</el-dropdown-item>
								</el-dropdown-menu>
							</el-dropdown>
						</div>
					</el-col>
				</el-row>
			</el-header>
			<el-container>
				<el-aside width="200px">
					<!--菜单-->
					<el-scrollbar wrapClass="scrollbar-wrapper" height="800px">
						<el-menu default-active="/Activity" class="el-menu-vertical" router @open="handleOpen" @close="handleClose" :collapse="isCollapse">
							<el-submenu index="1">
								<template slot="title">
									<i class="el-icon-location"></i>
									<span>活动管理</span>
								</template>
								<el-menu-item index="/Activity">活动管理</el-menu-item>
								<el-menu-item index="/Alert">选项12</el-menu-item>
								<el-menu-item index="1-3">选项13</el-menu-item>
								<el-menu-item index="1-4-1">选项14</el-menu-item>
							</el-submenu>
							<el-submenu index="2">
								<template slot="title">
									<i class="el-icon-menu"></i>
									<span>导航二</span>
								</template>
								<el-menu-item index="2-1">选项21</el-menu-item>
								<el-menu-item index="2-2">选项22</el-menu-item>
								<el-menu-item index="2-3">选项23</el-menu-item>
								<el-menu-item index="2-4">选项24</el-menu-item>
							</el-submenu>
							<el-submenu index="3">
								<template slot="title">
									<i class="el-icon-menu"></i>
									<span>导航三</span>
								</template>
								<el-menu-item index="3-1">选项31</el-menu-item>
								<el-menu-item index="3-2">选项32</el-menu-item>
								<el-menu-item index="3-3">选项33</el-menu-item>
								<el-menu-item index="23-4">选项34</el-menu-item>
							</el-submenu>
							<el-submenu index="4">
								<template slot="title">
									<i class="el-icon-menu"></i>
									<span>导航四</span>
								</template>
								<el-menu-item index="4-1">选项41</el-menu-item>
								<el-menu-item index="4-2">选项42</el-menu-item>
								<el-menu-item index="4-3">选项43</el-menu-item>
								<el-menu-item index="4-4">选项44</el-menu-item>
							</el-submenu>
							<el-submenu index="5">
								<template slot="title">
									<i class="el-icon-menu"></i>
									<span>导航五</span>
								</template>
								<el-menu-item index="5-1">选项51</el-menu-item>
								<el-menu-item index="5-2">选项52</el-menu-item>
								<el-menu-item index="5-3">选项53</el-menu-item>
								<el-menu-item index="5-4">选项54</el-menu-item>
							</el-submenu>
							<el-submenu index="6">
								<template slot="title">
									<i class="el-icon-menu"></i>
									<span>导航六</span>
								</template>
								<el-menu-item index="6-1">选项61</el-menu-item>
								<el-menu-item index="6-2">选项62</el-menu-item>
								<el-menu-item index="6-3">选项63</el-menu-item>
								<el-menu-item index="6-4">选项64</el-menu-item>
							</el-submenu>
						</el-menu>
					</el-scrollbar>
				</el-aside>
				<el-main>
					<router-view></router-view>
				</el-main>
			</el-container>
			<el-footer height="50px">Footer</el-footer>
		</el-container>
	</div>
</template>

<script>
	export default {
		name: 'Admin',
		data() {
			return {
				isCollapse: false
			}
		},
		methods: {
			handleOpen(key, keyPath) {
				console.log(key, keyPath);
			},
			handleClose(key, keyPath) {
				console.log(key, keyPath);
			},
			collapse: function () {
				if (this.isCollapse) {
					this.isCollapse = false;
				} else {
					this.isCollapse = true;
				}
			}
		}
	}
</script>

<style>
	body {
		margin: 0px;
		padding: 0px;
		border: 0px;
	}

	.el-aside {
		width: auto !important;
	}

	.el-menu-vertical:not(.el-menu--collapse) {
		width: 200px;
	}
	.el-menu-vertical{	
		height: 500px;
	}
	#app {
		font-family: 'Avenir', Helvetica, Arial, sans-serif;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-align: center;
		color: #2c3e50;
		position: absolute;
		height: 100%;
		width: 100%;
		background: #f5f5f5;
	}

	.container {
		position: relative;
		height: 100%;
	}

	.el-aside {
		overflow: hidden;
		background: rgb(48, 65, 86);
		box-shadow: 0 2px 3px hsla(0, 0%, 7%, .1), 0 0 0 1px hsla(0, 0%, 7%, .1);
	}

	.el-scrollbar {
		height: 100%;		
	}
	
	.el-scrollbar__wrap { 
		overflow-x: auto;
	}

	.el-menu-item,
	.el-submenu__title {
		color: rgb(191, 203, 217)
	}

	.el-submenu__title {
		text-align: left;
	}
	.el-submenu__title span {
		padding-left: 5px ;
	}
	.el-submenu__title:hover {
		background: #1C2438;
	}

	.el-menu-item:hover {
		background: #495060;
	}
	.el-menu-item.is-active{
		background: rgb(48, 65, 86);
	}
	.el-menu {
		background: rgb(48, 65, 86);
		border-right: none;
	}

	.el-header,
	.el-footer {
		background: #fff;
		color: #333;
		text-align: center;
		z-index: 99;
	}

	.el-header {
		line-height: 60px;
		box-shadow: -1px 5px 20px hsla(0, 0%, 7%, .1), 0 0 0 0px hsla(0, 0%, 7%, .1);
	}

	.left,
	.right {
		height: 60px;
	}

	.left {
		text-align: left;
	}

	i {
		cursor: pointer;
	}

	.right {
		text-align: right;
		padding-right: 50px;
	}

	.el-footer {
		line-height: 50px;
		box-shadow: 0px 0px 20px 0px hsla(0, 0%, 7%, .1), 0px 0px 0px 0px hsla(0, 0%, 7%, .1);
	}
</style>



