<template>
	<div>
		<div class="search">
			<el-form :inline="true" :model="search" class="demo-form-inline">
				<el-form-item label="活动名称">
					<el-input v-model="search.activityName" placeholder="活动名称"></el-input>
				</el-form-item>
				<el-form-item label="开始时间">
					<el-input v-model="search.startTime" placeholder="开始时间"></el-input>
				</el-form-item>

				<el-form-item>
					<el-button type="primary" @click="onSearch">查询</el-button>
				</el-form-item>
			</el-form>
		</div>
		<div class="table">
			<el-table v-loading="loading" element-loading-text="拼命加载中" element-loading-background="rgba(241, 240, 240, 0.7)" :data="tableData"
			    :height="height" border style="width: 100%">
				<el-table-column v-for="(column,index) in tableColumns" :key="index" :prop="column.prop" :label="column.label" :width="column.width" align="center">
				</el-table-column>
				<el-table-column align="center" label="操作">
					<template slot-scope="scope">
						<el-button icon="el-icon-edit" size="mini" @click="look(scope.$index, scope.row)">编辑</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>

		<div class="block">
			<el-pagination background :current-page.sync="currentPage" :page-size="pagesize" layout="total,sizes, prev, pager, next"
			    :total="total" @current-change="handleCurrentChange" @size-change="handleSizeChange">
			</el-pagination>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				search: {
					activityName: '',
					startTime: ''
				},
				tableColumns: [{
					prop: 'activityName',
					label: '活动名称',
					width: 200
				}, {
					prop: 'activityContent',
					label: '活动内容',
					width: 300
				}, {
					prop: 'users.phone',
					label: '手机号码',
					width: 200
				}, {
					prop: 'startTime',
					label: '开始时间',
					width: 250
				}, {
					prop: 'endTime',
					label: '结束时间',
					width: 250
				}],
				tableData: [],
				loading: true,
				dialogVisible: false,
				path: '',
				height: 400,
				total: 0,
				currentPage: 1,
				pagesize: 50
			};
		},
		created: function () {
			this.load()
		},
		methods: {
			load: function () {
				var context = this;
				var parameter = {
					currentPage: context.currentPage,
					pagesize: context.pagesize,
					activityName: context.search.activityName,
					startTime: context.search.startTime
				};
				this.$post("ActivityApi/queryLimit", parameter).then(function (respone) {
					console.log(respone)
				}).catch(function (err) {
					console.log(err)
				});
			},
			look: function (index, row) {
				context.path = "";
				context.dialogVisible = true;
				context.path = row.faceImage;
			},
			handleCurrentChange: function () {
				this.load();
			},
			handleSizeChange: function (size) {
				this.pagesize = size;
				this.load();
			},
			onSearch: function () {
				this.currentPage = 1;
				this.load();
			}
		}
	};
</script>



